import React, { Component } from 'react';

import "./creator.css";

class Creator extends Component {
    defaultState = {
        name: '',
        description: '',
        importance: '',
        dateDeadline: '',
        datePerformance: '',
        status: " ",
    };

    constructor(props) {
        super(props);
        this.state = {
            todo: props.selectedTodo ? props.selectedTodo : { ...this.defaultState }
        };
    }

    componentWillReceiveProps(nextProps) {
        if (this.state.todo !== nextProps.selectedTodo) {
            this.setState((prevState) => {
                return {
                    ...prevState,
                    todo: nextProps.selectedTodo ? nextProps.selectedTodo : this.defaultState
                };
            })
        }
    };

    handlerAddTodo = () => {
        let todoName = this.state.todo.name;
        if (todoName) {
            const todo = {
                id: Date.now(),
                name: todoName,
                description: this.state.todo.description,
                importance: this.state.todo.importance,
                dateDeadline: Date.parse(this.state.todo.dateDeadline) || '-',
                datePerformance: this.state.todo.datePerformance,
                status: this.state.todo.status
            };
            this.props.addTodo(todo);
            this.resetForm();
        }
    };

    handlerUpdateTodo = () => {
        let todoName = this.state.todo.name;
        if (todoName) {
            let todo = {
                id: this.state.todo.id,
                name: todoName,
                description: this.state.todo.description,
                importance: this.state.todo.importance,
                dateDeadline: Date.parse(this.state.todo.dateDeadline) || '-',
                datePerformance: '',
                status: " "
            };
            this.props.updateTodo(todo);
            this.resetForm();
        }
    };

    handleChange = (event) => {
        const todo = { ...this.state.todo };
        const { name, value } = event.target;
        todo[name] = value;
        this.setState({ todo: todo });
    };

    resetForm() {
        this.setState({
            todo: { ...this.defaultState },
        });
    }

    renderBtn = () => {
        const addBtn = <button type="button" className="creator__add" onClick={this.handlerAddTodo}></button>;
        const saveBtn = <button type="button" className="creator__save" onClick={this.handlerUpdateTodo}></button>;
        if (this.state.todo.id) {
            return saveBtn
        } else {
            return addBtn
        }
    };

    render () {
        return (
            <div className="creator">
                <div className="container">
                    <div className="row">
                        <div className="col-md-6">
                            <input className="creator__name"
                                   placeholder=" "
                                   name='name'
                                   value={this.state.todo.name}
                                   onChange={this.handleChange} />
                        </div>
                        <div className="col-sm-6 col-md-3">
                            <label> :
                                <select className="creator__importance"
                                        name='importance'
                                        value={this.state.todo.importance}
                                        onChange={this.handleChange}>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </label>
                        </div>
                        <div className="col-sm-6 col-md-3">
                            <label> :
                                <input type="date"
                                       className="creator__date-deadline"
                                       name='dateDeadline'
                                       value={this.state.todo.dateDeadline}
                                       onChange={this.handleChange} />
                            </label>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12">
                            <textarea className="creator__description"
                                      placeholder=" "
                                      name="description"
                                      value={this.state.todo.description}
                                      onChange={this.handleChange} />
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12 creator__action">
                            {this.renderBtn()}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Creator;
